.Wrapper
{
    width: 900px;
    height: 300px;
}

.Wrapper__img{
    max-width:100%;
    max-height:100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
    height: 180px;
    min-width: 100%;
    min-height: 100%;
    flex-shrink: 0;
}
.CardSpecPrice__price-block{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.CardSpecPrice__price{

    display: block

}
.ui.grid>.row>.column, .ui.grid>.column{
display: flex;
}
